import React from 'react';
const CircleSlash = (props) => {
    let left = props.left ? props.left : 0;
    let top = props.top ? props.top : 0;
    let size = props.size ? props.size : 16;
    let color = props.color ? props.color : 'currentColor';
    return (
        <svg xmlns="http://www.w3.org/2000/svg" width={size} height={size} fill={color} style={{transform:"translate("+left+"px, "+top+"px)"}} className="bi bi-slash-circle" viewBox="0 0 16 16">
          <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z"/>
          <path d="M11.354 4.646a.5.5 0 0 0-.708 0l-6 6a.5.5 0 0 0 .708.708l6-6a.5.5 0 0 0 0-.708z"/>
        </svg>
    )
};

export default CircleSlash;
